.header {
  padding: 60px;
  text-align: center;
  background: #1abc9c;
  color: white;
  font-size: 30px;
}

.headerButtons {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
}

.headerButtons h6 {
  background: gray;
  width: 75%;
  margin: 2px;
  border-radius: 2px;
}
img {
  object-fit: contain;
  width: 150px;
  height: 100px;
}

.pictures {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.colorScheme {
  text-align: center;
}
.colors{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

#color1 {
  height: 150px;
  background: slateblue;
}
#color2 {
  background: blue;
}
#color3 {
  background: slategrey;
}
#color4 {
  background: skyblue;
}
#color5 {
  background: darkblue;
}


.bodyRow {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.bodyCol {
  text-align: center;
}
